* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  background-image: linear-gradient(to top, #a8edea 0%, #fed6e3 100%);  
  /* overflow: hidden; */
}

.Iphone {
  width: 400px; 
  height: 700px;
  margin: 1rem auto  1rem auto;
  border-radius: 40px;
  border: 60px;
  background-color: #000000;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.3);
}

/* .Iphonescreen {
  width: 100%;
  height: 100%;
  background-color: #fff;
  padding: 10px;
  overflow: auto;
} */

.homeline{
  width: 90px;
  height: 5px;
  border-radius: 10dvb;
  background-color: grey;
  position: relative;
  top: 28rem;
  left: 8rem; 
}


.app {
  background-image: url('./assets/warm-bg.jpg');
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease;
}

.app.cold{
  background-image: url('./assets/winter.jpg');
}
.app.warm {
  background-image: url('./assets/summer.jpg');
}

main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.75));
  padding: 25px;
}
.time{
  /* color: rgb(178, 177, 177); */
  color: whitesmoke;
  font-size: 19px;
  display: inline;
  width:75px;
  height: 30px;
  position: relative;
  top: 0.5rem auto;
  left: 20rem auto;
  text-shadow: 3px 3px rgba(10, 10, 20, 0.05);

}

.search-box {
  width: 100%;
  margin: 110px 0 51px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 16px 16px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  
  color: #313131;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #FFF;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 22px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 7px 20px;

  color: #FFF;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #FFF;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
